import './shop.css'
import cancel from '../assets/cancel.svg';

const Cart = (props) => {
    const {cart, remove, removeAll} = props;

    const countTotal = () => {
        let total = 0
        Array.from(cart).map(obj => {total+=obj.qty*obj.price})
        return total
    }
    const countTotalQty = () => {
        let total = 0
        Array.from(cart).map(obj => {total+=obj.qty})
        return total
    }
    return (
        <div className='mct__shop-cart'>
            {cart.length === 0 ? <div>Ничего нет.
                <p>Нажмите "Добавить" под продуктом слева, чтобы добавить его в корзину.</p>
            </div> : 
                    <div>
                        <table>
                            <tr>
                                <th>Товар</th>
                                <th>Кол-во</th>
                                <th>Стоимость</th>
                                <th className='last'>Убрать</th>
                            </tr>
                        {Array.from(cart).map(obj => {
                        return (
                            
                            <tr>
                                <td>{obj.item}</td> <td>{obj.qty}</td> <td>{obj.price*obj.qty}</td>
                                <td className='td_button'><button onClick={()=>remove(obj, obj.qty)}><img src={cancel} alt="Cancel"></img></button></td>
                            </tr>
                            
                        )
                        })}
                        <tr className='cart_total'><td className='total'>Итого:</td><td>{cart.length}({countTotalQty()})</td><td>{countTotal()}</td>
                        <td className='td_button'><button onClick={()=>removeAll()}><img src={cancel} alt="Cancel"></img></button></td></tr>
                        </table>
                    </div>
            }
        </div>
    )
}

export default Cart;